@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
body,html{margin:0;font-size:18px}body{padding-bottom:45px}*{font-family:'Montserrat';box-sizing:border-box;outline:none;padding:0;margin:0}.header{background:#252830;padding:15px;display:flex;align-items:center}.title{color:#fff;margin-left:15px}.search-container{display:flex;background:#fafafa;-webkit-transition:all 0.3s;transition:all 0.3s;position:-webkit-sticky;position:sticky;top:0;z-index:10}.search-container:hover,.search-container:focus-within{background:#f2f2f2}.search,.form-action,.sort,.close{border:0;background:transparent;border-radius:0}.search{flex:1 1;font-size:22px;padding:15px;padding-left:0}.form-action{font-size:20px;padding:0 15px;cursor:pointer;color:#bbb;-webkit-transition:all 0.3s;transition:all 0.3s}.form-action:hover{color:#252830}.sort{min-width:10%;font-size:18px;margin-left:15px;border-left:2px solid #ebebeb}.grid{display:grid;grid-template-columns:repeat(auto-fill, 185px);grid-gap:30px;gap:30px;justify-content:center;margin:45px 0}.movie-card{text-align:center;cursor:pointer;border-radius:8px;padding-bottom:10px;-webkit-transition:all 0.3s;transition:all 0.3s}.movie-card:hover .movie-poster{box-shadow:0px 8px 40px 20px rgba(37,40,48,0.2);-webkit-transform:scale(1.02);transform:scale(1.02)}.movie-poster{max-width:100%;border-radius:8px;box-shadow:0px 8px 20px rgba(0,0,0,0.2);-webkit-transition:all 0.3s;transition:all 0.3s}.movie-title{margin:0;padding:10px;-webkit-transition:none;transition:none}.rating{color:#f1c40f}.loader{border:8px solid #f3f3f3;border-radius:50%;border-top:8px solid #252830;width:60px;height:60px;margin:0 auto;-webkit-animation:spin 1s linear infinite;animation:spin 1s linear infinite}@-webkit-keyframes spin{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes spin{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}.load-more{background:transparent;border:2px solid #252830;color:#252830;-webkit-transition:all 0.3s;transition:all 0.3s;font-size:16px;font-weight:bold;text-transform:uppercase;display:block;padding:15px 30px;border-radius:30px;margin:0 auto;cursor:pointer}.load-more:hover{background:#252830;color:#fff}.modal{position:fixed;top:0;height:100vh;width:100vw;z-index:99;background:rgba(0,0,0,0.4);display:flex;justify-content:center;align-items:center}.modal-dialog{background:#fff;display:inline-block;border-radius:5px;box-shadow:0px 8px 20px rgba(0,0,0,0.4);min-width:40%;max-width:50vw;max-height:80vh;overflow:auto;padding:45px;position:relative}.modal-enter{opacity:0}.modal-enter .modal-dialog{-webkit-transform:translateY(-30px);transform:translateY(-30px)}.modal-enter-active{opacity:1;-webkit-transition:opacity 300ms;transition:opacity 300ms}.modal-enter-active .modal-dialog{-webkit-transform:translateY(0);transform:translateY(0);-webkit-transition:-webkit-transform 300ms;transition:-webkit-transform 300ms;transition:transform 300ms;transition:transform 300ms, -webkit-transform 300ms}.modal-exit{opacity:1}.modal-exit-active{opacity:0;-webkit-transition:opacity 300ms;transition:opacity 300ms}.modal-exit-active .modal-dialog{-webkit-transform:translateY(-30px);transform:translateY(-30px);-webkit-transition:-webkit-transform 300ms;transition:-webkit-transform 300ms;transition:transform 300ms;transition:transform 300ms, -webkit-transform 300ms}.close{display:block;position:absolute;top:15px;right:15px;color:#ccc;font-size:22px;cursor:pointer}.movie-details{display:flex}.movie-details .movie-poster{margin-right:25px}.movie-details .movie-title{padding:0}.movie-details .release{margin:5px 0}.movie-details .genres{display:flex;font-size:12px;margin-bottom:5px}.movie-details .tagline{font-style:italic;margin-top:20px}.movie-details .overview{font-weight:normal;margin-top:20px}

